import { EventEmitter } from 'events';
import utils from '../../../utils';
import { SimulationError, SimulationNeedInputError } from '../asmErrors';
import Instruction from '../instruction';

const endSimulationCode = 0x0003;
const defaultBreakpointHandler = (simulation, pc) => false;

export default class Simulation extends EventEmitter {
    constructor(architecture, memory, registerBank) {
        super();

        this.architecture = architecture;
        this.memory = memory;
        this.registerBank = registerBank;
        this.codeExecutionMaxPC = 0;
        this.running = false;
        this.ended = false;
        this.stopping = false;
        this.waitingInput = false;
        this.stepInterval = 50;
        this.cycles = 0;
        this.pc = 0;
        this.inputBytes = [];

        this.breakpointHandler = defaultBreakpointHandler;
        this.setupMemoryHandlers();
    }

    reset() {
        if (this.running && !this.stopping) this.stop();
        this.ended = false;
        this.carry = 0;
        this.setPC(0);
        this.setCycles(0);
        this.resetInput();
        this.registerBank.reset();
        this.registerBank.setValue('sp', 0xdffe);
        this.memory.reset();
        this.writeObjCodeMemory();
        this.emit('reset');
    }

    setupMemoryHandlers() {
        this.memory.onReadWord = (address) => {
            if (address < 0xe000) return; // ok

            switch (address) {
                case 0xf004:
                    if (this.inputBytes.length < 1) {
                        this.setWaitingInput(true);
                        throw new SimulationNeedInputError('Input too short, need 1 byte');
                    }
                    return this.readInputChar();
                case 0xf006:
                    if (this.inputBytes.length < 2) {
                        this.setWaitingInput(true);
                        throw new SimulationNeedInputError('Input too short, need 2 bytes');
                    }
                    return this.readInputInt();
                default:
                    throw new SimulationError('read on unauthorized location [' + address.toString(16) + ']');
            }
        };
        
        this.memory.onWriteWord = (address, value) => {
            if (address < 0xe000) return true; // ok

            switch (address) {
                case 0xf000:
                    this.emit('console write char', String.fromCharCode(value));
                    return true;
                case 0xf002:
                    this.emit('console write int', utils.unsignedToSigned(value));
                    return true;
                default:
                    // write on unauthorized location
                    this.emit('run error', new SimulationError('write on unauthorized location [' + address.toString(16) + '] = ' + value.toString(16)));
                    this.stop();
                    return false;
            }
        };
    }

    readInputChar() {
        if (this.inputBytes.length === 0) throw new SimulationNeedInputError('Empty input');
        let byte = this.getNextInputByte();
        return byte;
    }
    
    readInputInt() {
        if (this.inputBytes.length === 0) throw new SimulationNeedInputError('Empty input');
        let zeroCharCode = '0'.charCodeAt(0);
        let nineCharCode = '9'.charCodeAt(0);
        let minusCharCode = '-'.charCodeAt(0);

        function isMinusChar(c) {
            return minusCharCode === c;
        }

        function isNumberChar(c) {
            return c >= zeroCharCode && c <= nineCharCode;
        }

        function charToNumber(c) {
            return c - zeroCharCode;
        }

        let int = 0;
        let firstC = this.getNextInputByte();
        if (isNumberChar(firstC) || isMinusChar(firstC)) {
            if (isNumberChar(firstC)) {
                int = charToNumber(firstC);
            }

            let length = this.inputBytes.length;
            for (let i = 0; i < length; i++) {
                let c = this.getNextInputByte();
                if (isNumberChar(c)) {
                    let number = charToNumber(c);
                    int *= 10;
                    int += number;
                } else {
                    if (c === 0) {
                        this.getNextInputByte();
                    }
                    break;
                }
            }

            if (isMinusChar(firstC)) {
                int *= -1;
            }
        } else {
            // l os bytes do inputs at acabar ou at o byteWidth da arquitetura
            // se o inputBytes tiver 1 byte e o byteWidth da arquitetura for 2 bytes, 16 bits, vai ler apenas 1 byte
            // se o inputBytes tiver 5 bytes e o byteWidth da arquitetura for 2 bytes, vai ler apenas 2 bytes
            int = firstC;
            let length = Math.min(this.architecture.getByteWidth() - 1, this.inputBytes.length);
            for (let i = 0; i < length; i++) {
                let c = this.getNextInputByte();
                this.emit('input buffer', this.inputBytes);
                int = (int << 8) | c;
            }
        }
        
        return int;
    }

    setRawObjCode(rawObjCode) {
        if (rawObjCode.length > 0xf000 * 2) throw new SimulationError('Object code too big for the memory');
        this.rawObjCode = rawObjCode;
    }

    writeObjCodeMemory() {
        if (!this.rawObjCode) return;
        let wordArray = this.rawObjCode
            .match(/.{1,4}/g)
            .map(x => parseInt(x, 16));
        for (let i = 0; i < wordArray.length; i++) {
            let word = wordArray[i];
            this.memory.writeWord(i*2, word);
        }

        this.codeExecutionMaxPC = wordArray.length * 2;
    }

    step() {
        if (this.pc >= this.codeExecutionMaxPC) throw new SimulationError('PC run out of program bounds');

        let code;

        try {
            code = this.memory.readWord(this.pc);
        } catch (exc) {
            console.error(exc);
            this.emit('run error', exc);
            return this.stop();
        }
        
        if (code === endSimulationCode) {
            this.ended = true;
            this.emit('run ended');
            return this.stop();
        }

        try {
            let instruction = Instruction.disassemble(code, this.architecture);
            instruction.execute(this);
        } catch (exc) {
            if (exc.name === 'SimulationNeedInputError') {
                return this.stop();
            }
            console.error(exc);
            this.emit('run error', exc);
            return this.stop();
        }

        this.incrementPC(2);
        this.incrementCycles(1);
        
        if (this.breakpointHandler(this, this.pc)) {
            this.emit('breakpoint', this.pc);
            this.stop();
        }
    }

    async runner() {
        this.emit('run started');
        try {
            let timePerBlock = 50;
            let toExecFloat = 0;
            runLoop:
            while(!this.stopping) {
                if (this.stepInterval > 0) {
                    toExecFloat += timePerBlock / this.stepInterval;
                    if (toExecFloat >= 1) {
                        let toExec = Math.floor(toExecFloat);
                        toExecFloat -= toExec;
                        let start = Date.now();
                        for (let i = 0; i < toExec; i++) {
                            if (this.stopping) break runLoop;
                            this.step();
                        }
                        let sleepTime = timePerBlock - (Date.now() - start);
                        if (sleepTime > 0) await utils.sleep(sleepTime);
                    } else {
                        await utils.sleep(timePerBlock);
                    }
                } else {
                    this.step();
                }
            }
        } catch (error) {
            console.error(error);
            this.emit('run error', error);
        }
        this.running = false;
        this.stopping = false;
    }

    stop() {
        if (!this.running) throw new Error('Simulation already stopped');
        if (this.stopping) throw new Error('Simulation already stopping');
        this.stopping = true;
    }

    run() {
        if (this.running) throw new Error('Simulation already running');
        if (this.stopping) throw new Error('Simulation stopping');
        this.running = true;
        this.stopping = false;
        setTimeout(this.runner.bind(this));
    }

    isWaitingInput() {
        return this.waitingInput;
    }

    isRunning() {
        return this.running;
    }

    isStopping() {
        return this.stopping;
    }

    hasEnded() {
        return this.ended;
    }

    getNextInputByte() {
        let byte = this.inputBytes.shift();
        this.emit('input buffer', this.inputBytes);
        return byte;
    }

    resetInput() {
        this.inputBytes.length = 0;
        this.emit('input buffer', this.inputBytes);
        this.setWaitingInput(false);
    }

    addInput(inputBytes) {
        for (let byte of inputBytes) 
            this.inputBytes.push(byte & 0xFF);
        this.emit('input buffer', this.inputBytes);
        if (this.isWaitingInput()) {
            this.setWaitingInput(false);
            this.run();
        }
    }

    getInput() {
        return this.inputBytes;
    }

    getRegisterBank() {
        return this.registerBank;
    }

    getMemory() {
        return this.memory;
    }

    getCarry() {
        return this.carry;
    }

    setWaitingInput(waitingInput) {
        this.waitingInput = waitingInput;
        this.emit('waiting input', this.waitingInput);
    }

    setCarry(carry) {
        this.carry = carry;
    }

    setPC(pc) {
        this.pc = (pc >>> 0) & this.architecture.getMask();
        this.emit('pc update', this.pc);
    }

    incrementPC(value) {
        this.setPC(this.getPC() + value);
    }

    getPC() {
        return this.pc;
    }
    
    setCycles(cycles) {
        this.cycles = cycles;
        this.emit('cycles update', this.cycles);
    }

    incrementCycles(value) {
        this.cycles += value;
        this.emit('cycles update', this.cycles);
    }

    getCycles() {
        return this.cycles;
    }

    getArchitecture() {
        return this.architecture;
    }

    setBreakpointHandler(handler) {
        if (!handler) return this.breakpointHandler = defaultBreakpointHandler;
        if (typeof handler != 'function') throw new Error('handler isnt function');
        this.breakpointHandler = handler;
    }

    setStepInterval(ms) {
        this.stepInterval = ms;
    }

    getStepInterval() {
        return this.stepInterval;
    }
}